body {
    margin: 0;
}

.form_window {
    margin-top: 70px;
    margin-bottom:20px;
    width: 510.5px;
    min-height: 95%;
    background: rgb(225, 235, 235);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 40px;
}
.container_table{
    margin-top:60px;
}
.form_header {
    width: 100%;
    text-align: center;
    border-radius: 10px 10px 0 0;
    background-color: rgb(37, 156, 22);
}
.Parag_Description{
    grid-column-start: 1;
    grid-column-end:3 ;
    text-align: center;
    font-size: 20px;
}
.Input_Owner_Description {
    display: block;
    width: 120px;
    height: 30px;
    border-bottom: 1px solid black;
}
.Input__Description__little__all{
    display: grid;
    grid-template-columns: repeat(2,50%);
    border-top:1px solid black ;
    border-bottom:1px solid black  ;
    
}
.Window_input {
    width: 384px;
    display: block;
    height: 32px;
    top: 275px;
    background: rgba(103, 170, 170, 0.39);
    border: 0;
    border-bottom: solid black 1px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    font-size: 18px;
}

.Window_input__little{
    width: 130px;
    display: block;
    height: 32px;
    top: 275px;
    background: rgba(103, 170, 170, 0.39);
    border: 0;
    border-bottom: solid black 1px;
    font-size: 18px;
    margin-bottom: 10px;
    

}
.Window_div__little{
    width: 140px;
    display: block;
    height: 32px;
    top: 275px;
    background: rgba(103, 170, 170, 0.39);
    border: 0;
    border-bottom: solid black 1px;
    font-size: 20px;
    margin-bottom: 10px;
    margin-left: 63px;
    align-items: center;
    text-align: end;
}
.select {
    width: 384px;
    display: block;
    height: 32px;
    top: 275px;
    border: 0;
    background: rgba(103, 170, 170, 0.39);
    border-bottom: solid black 1px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    font-size: 18px;
}

.Input__Description {
    margin-left: 65px;
    font-style: normal;
    font-weight: 200;
    font-size: 20px;
    line-height: 164%;
    margin-bottom: 0;
}
.Input__Description__little{
    font-weight: bold;
    margin-left: 62px;
}
.save {
    background-color: rgb(0, 255, 157);
    display: inline;
    width: 130px;
    height: 42px;
    margin-top: 0px;
    margin-left: 60px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    border: none;
}


:hover.save {
    cursor: pointer;
}

.modal {
    top: 0;
    left: 0;
    position: absolute;
    min-width: 350px;
    min-height: 200px;
    background: rgb(241, 241, 241);
    box-shadow: 1px 2px 5px 4px rgba(0, 0, 0, 0.404);
}

.header {
    width: 100%;
    height: 50px;
    background: #30aa63;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.25);
}
.header h3{
    color:  white;
    width: 100%;
    text-align: center;
    font-size: 25px;
}
.icon{
    font-size: 75px;
    text-align: center;
    color: #30aa63;
    margin-top:15px;
}

.close, .close__edit {
    margin-left: auto;
    margin-right: auto;
    margin-top: 85px;
    display: block;
    position: flex;
    cursor: pointer;
    width: 130px;
    height: 40px;
    background-color: #eb0000;
    border: none;
    font-size: 20px;
    
}
.close__edit{
    margin-top: -80px;
    margin-right: 50px;
    margin-left: auto;
}
.dialog_window_body{
    margin-top: -30px;
    padding-bottom: -40px;

}




h1 {
    text-align: center;
    background-color: rgba(225, 235, 235, 0.658);
    height: 50px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
}

.field_table {
    background-color: rgb(219, 216, 216);
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    border-spacing: 0;
    margin-bottom: 70px;
}

th, td {
    padding-top: 10px;
    padding-bottom: 10px;
    border: none;
    text-align: start;
    padding-left: 5px;
    width: 16%;
    
}
thead> tr{
    font-size: 18px;
    background-color: rgb(149, 219, 161);
    color: rgb(0, 0, 0);
}
.field_table_body {
    font-size: 18px;
}

tbody>tr:nth-child(even) {
    background-color: white;
}

tbody>tr:nth-child(odd) {
    background-color:rgb(235, 235, 235);
}
.Del_field{
    background-color:red ;
    margin-right: 10px;
    padding: 5px 12px;
    font-size: 15px;
    color: white;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition-duration: 0.4s;
    transition-property: color;
}
.Card_field{
    background-color:rgb(33, 146, 39) ;
    padding: 5px 12px;
    margin-right: 10px;
    font-size: 15px;
    color: white;
    font-weight: bold;
    border: none;
    cursor: pointer;
}
#button{
    display: flex;
}

