body {
    margin: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: bottom;
    background-attachment: fixed;
    
}
:root{
    --color-background:rgb(197, 197, 197);
}
nav{
    min-height: 40px;
    width: 100%;
    background: #147421;
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    right: 0;
    z-index: 2;
    box-shadow: 0 1px 10px 3px;
}
.navigation__menu {
    list-style: none;
    display: flex;
    width: 90%;
    margin-left: 10px;
    justify-content: space-around;
}
.navigation__mobile{
    display: none;
}

:hover #navigation_button{
    cursor: pointer;  
}
#navigation_button{
    text-shadow: 1px 1px 2px black;
}
:hover.link{
    cursor: pointer;
    text-shadow: 1px 1px 2px rgb(33, 175, 168);
}
nav.Navigation a.active {
    color: red;
}
#navigation__menu_item_logout{
    width: 180px;
    background: #cf2a2a;
    border-radius: 11px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    height: 29px;
    color:#000 ;
}
:hover#navigation__menu_item_logout{
    cursor: pointer;
    text-shadow: none;
    border-bottom: none;
}
.link{
    font-size: 20px;
    color: white;
    text-shadow: 1px 1px 2px black;
    text-decoration: none;
}
.MainTraining{
    margin-top: 88px;
    background-color: var(--color-background)


}
.MainTraining_section{  
    min-width: 220px;
    min-height: 250px;
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: rgb(225, 235, 235);
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 10px 2px;

    

}

.MainTraining_header{
    text-align: center;
    font-size: 28px;
    grid-column-start: 1;
    grid-column-end:3 ;
    display: block;
    height: 40px;
    margin-top: 0;

}
.f_table_head{
    font-size: 18px;
    background-color: rgb(149, 219, 161);
}
#t_stat, #t_ostatnie{
    width: 95%;
    margin-left: auto;
    margin-right: auto;
}
#stat_head{
    background-color: rgb(149, 219, 161);
    font-size: 22px;
}

.f_table_element{
    font-size: 20px;
    background-color: rgb(218, 218, 218);
}
