body {
    margin: 0;
}
.RegisterPage{
    display: grid;
    margin: 0;
    padding: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: left;
    background-position-y: bottom;
    background-attachment: fixed;
    min-height: 100vh;
    height: auto;
    margin-bottom: 60px;
}
.Register_window {
    margin-top: 30px;
    width: 510.5px;
    height: 650px;
    background: #E5E5E5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 40px;
    
}

